import { HttpClient } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DataTableDirective } from 'angular-datatables';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ExportAsConfig, ExportAsService } from 'ngx-export-as';
import { NgxRolesService } from 'ngx-permissions';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { DatatablesResponse } from 'src/app/core/helpers/datatables-response.helper';
import { AuthenticationService } from 'src/app/core/services/authentication.service';
import { environment } from 'src/environments/environment';
import { MenuCreateComponent } from '../../components/menu-create/menu-create.component';
import { MenuEditComponent } from '../../components/menu-edit/menu-edit.component';

@Component({
  selector: 'app-menu-list',
  templateUrl: './menu-list.component.html',
  styleUrls: ['./menu-list.component.scss']
})
export class MenuListComponent implements OnInit {
  public modalRef: BsModalRef;
  @ViewChild(DataTableDirective, { static: true })
  dtElement: DataTableDirective;
  dtTrigger: Subject<any> = new Subject();
  dtOptions: any = {};
  menus = [];
  loginUser: any;
  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'menu_table',
  };
  modalConfigs = {
    animated: true,
    keyboard: true,
    backdrop: true,
    ignoreBackdropClick: false,
    class: 'inmodal modal-dialog-centered modal-lg animated fadeIn'
  };
  private apiUrl = environment.apiUrl;
  constructor(
    public translate: TranslateService,
    public ngxRolesService: NgxRolesService,
    private http: HttpClient,
    private toastr: ToastrService,
    private modalService: BsModalService,
    private exportAsService: ExportAsService,
    private authenticationService: AuthenticationService,
  ) {
    this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
  }

  ngOnInit() {
    this.loadItemDatatable();
  }
  loadItemDatatable() {
    let that = this;
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: that.loginUser.settings.tables_pagination_limit,
      serverSide: true,
      processing: true,
      dom: '<"html5buttons"B>ltfrtip',
      order: [0],
      columns: [{
        'sortable': true,
        'width': '',
        'target': [0]
      }, {
        'sortable': true,
        'width': '',
        'target': [1]
      }, {
        'sortable': false,
        'width': '',
        'target': [2]
      }],
      buttons: [{
        extend: 'csv',
        className: 'btn btn-datatable-gredient',
        action: function (e, dt, node, config) {
          that.exportFiles('csv');
        }
      }, {
        extend: 'excel',
        className: 'btn btn-datatable-gredient',
        action: function (e, dt, node, config) {
          that.exportFiles('xlsx');
        }
      }, {
        extend: 'pdf',
        className: 'btn btn-datatable-gredient',
        action: function (e, dt, node, config) {
          that.exportFiles('pdf');
        }
      }],
      language: {
        'sEmptyTable': this.translate.instant('common.datatable.sEmptyTable'),
        'sInfo': this.translate.instant('common.datatable.sInfo'),
        'sInfoEmpty': this.translate.instant('common.datatable.sInfoEmpty'),
        'sSearch': '',
        'sInfoPostFix': this.translate.instant('common.datatable.sInfoPostFix'),
        'sInfoThousands': this.translate.instant('common.datatable.sInfoThousands'),
        'sLengthMenu': this.translate.instant('common.datatable.sLengthMenu'),
        'sLoadingRecords': this.translate.instant('common.datatable.sLoadingRecords'),
        'sProcessing': this.translate.instant('common.datatable.sProcessing'),
        'sZeroRecords': this.translate.instant('common.datatable.sZeroRecords'),
        'sSearchPlaceholder': this.translate.instant('common.datatable.sSearchPlaceholder'),
        'oPaginate': {
          'sFirst': this.translate.instant('common.datatable.oPaginate.sFirst'),
          'sLast': this.translate.instant('common.datatable.oPaginate.sLast'),
          'sNext': this.translate.instant('common.datatable.oPaginate.sNext'),
          'sPrevious': this.translate.instant('common.datatable.oPaginate.sPrevious')
        },
        'oAria': {
          'sSortAscending': this.translate.instant('common.datatable.oAria.sSortAscending'),
          'sSortDescending': this.translate.instant('common.datatable.oAria.sSortDescending')
        }
      },
      ajax: (dataTablesParameters: any, callback) => {
        this.http.post<DatatablesResponse>(this.apiUrl + '/api/all-items', dataTablesParameters, {}).subscribe(resp => {
          if (resp) {
            this.menus = []
          }
          callback({
            recordsTotal: resp.recordsTotal,
            recordsFiltered: resp.recordsFiltered,
            data: [],
          });
        });
      }
    };
  }
  exportFiles(type) {
    this.exportAsConfig.type = type;
    this.exportAsService.save(this.exportAsConfig, this.translate.instant('menu.title')).subscribe(() => {
    });
  }
  openMenuCreateModal(){
    this.modalRef = this.modalService.show(MenuCreateComponent, this.modalConfigs);
    // debugger
    this.modalRef.content.event.subscribe(data => {
        this.rerender();
    });
  }
  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
        dtInstance.destroy();
        setTimeout(() => {
            this.dtTrigger.next();

            if (this.menus.length > 0) {
                $('.tfoot_dt').addClass('d-none');
            } else {
                $('.tfoot_dt').removeClass('d-none');
            }
        });
    });
  }
  openMenuEditModal(item) {
    let modalConfig = {
        animated: true,
        keyboard: true,
        backdrop: true,
        ignoreBackdropClick: false,
        class: 'inmodal modal-dialog-centered modal-lg animated fadeIn',
        initialState: {
            item: item
        }
    };

    this.modalRef = this.modalService.show(MenuEditComponent, modalConfig);
    this.modalRef.content.event.subscribe(data => {
        this.rerender();
    });
  }
  deleteMenu(item){

  }
}
