import { DatePipe } from '@angular/common';
import { Component, EventEmitter,OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-sub-menu-edit',
  templateUrl: './sub-menu-edit.component.html',
  styleUrls: ['./sub-menu-edit.component.scss']
})
export class SubMenuEditComponent implements OnInit {

  public event: EventEmitter<any> = new EventEmitter();
  public onClose: Subject<boolean>;
  editSubMenuForm: FormGroup;
  isFormSubmitted = false;
  isFormLoaded = false;
  types: Array<any>;
  pages: Array<any>;
  pages_type: Array<any>;
  constructor(
    public translate: TranslateService,
    public bsModalRef: BsModalRef,
    private formBuilder: FormBuilder,
    private toastr: ToastrService,
    private modalService: BsModalService,
    private datepipe: DatePipe,
  ) { }
  get subMenuControl() {
    return this.editSubMenuForm.controls;
  }
  ngOnInit() {
    this.onClose = new Subject();
    this.loadForm();
    this.pages_type = [
      { 
        id:"1",
        name_type:"container"
      },
      { 
        id:"2",
        name_type:"single page"
      },
      { 
        id:"3",
        name_type:"category"
      },
      { 
        id:"4",
        name_type:"collection"
      },
      { 
        id:"5",
        name_type:"news"
      },
      {
        id:"6",
        name_type:"product"
      }
    ],
    this.pages = [
    ]
  }
  loadForm() {
    this.editSubMenuForm = this.formBuilder.group({
      name: [null, Validators.required],
      page_type: [null, Validators.required],
      page: [null, Validators.required],
      index: [null, Validators.required],
    });

    this.isFormLoaded = true;
  }
  onSubmit() {
    this.isFormSubmitted = true;
    if (this.editSubMenuForm.invalid) {
      return;
    }
    console.log(this.editSubMenuForm.value);
    // this.seoService.create(this.createSeoForm.value).subscribe(
    //         data => {
    //             this.toastr.success(this.translate.instant('items.messages.create'), this.translate.instant('items.title'));
    //             this.event.emit({data: true});
    //             this.onCancel();
    //         }, error => {
    //             this.onCancel();
    //         });
  }
  onCancel() {
    this.onClose.next(false);
    this.bsModalRef.hide();
  }

}
